// three.js
import * as THREE from '../../../util/three.module.js';
// pointsArrspointsArrs
// png
var guangq = require('./guangquan.png')
var geometry = new THREE.PlaneBufferGeometry(1, 1); //XOY
var textureLoader = new THREE.TextureLoader(); // TextureLoader
var texture = textureLoader.load(guangq);


// mesh

// size:Mesh
// xy
function shapeMesh (size, x, y, color) {
    var material = new THREE.MeshBasicMaterial({
        color: color,//
        map: texture,
        transparent: true, //png
        side: THREE.DoubleSide, //
    });
    var mesh = new THREE.Mesh(geometry, material);
    mesh.scale.set(size, size, size);//mesh
    mesh.position.set(x, y, 0);//mesh
    return mesh;
}
export { shapeMesh };