<template>
    <div class="threeLine" :ref="ref">
        <div class="dev-border" id='threeline1' :width="width" :height="height">

        </div>

    </div>
</template>

<script>
import * as THREE from "../../../util/three.module";
import { OrbitControls } from "../../../util/OrbitControls";
import autoResize from '../../../mixin/autoResize';
import { deepMerge, deepClone } from '../../../util/index'
import { CSS2DRenderer, CSS2DObject } from '../../../util/CSS2DRenderer.js';
import { CSS3DRenderer, CSS3DObject } from '../../../util/CSS3DRenderer.js';
import { tag, createLabelRender } from '../three3dTool/tag.js';
import { shapeMesh } from '../three3dTool/shapeMesh.js';


export default {
    name: 'threeLine',
    mixins: [autoResize],
    props: {
        color: {
            type: Array,
            default: () => { [] },

        },
        backgroundColor: {
            type: String,
            default: '#dddddd'
        },
        fontSize: {
            type: String,
            default: '16px'
        },
        textName: {
            type: Array,
            default: function () {
                return ['name1', 'name2']
            }
        },
        control3d: {
            type: Boolean,
            default: true

        }

    },
    data () {
        return {
            ref: 'threeLine',
            defaultColor: [0x00ffc0, 0x00a08b],
            mergedColor: [],
        }
    },
    computed: {

    },
    mounted () {
        this.$nextTick(() => {

            this.mergeColor();
            this.initLine();
        })


    },
    methods: {
        deepClone,
        deepMerge,
        mergeColor () {
            if (this.color && this.color.length) {
                this.mergedColor = this.deepMerge(this.deepClone(this.defaultColor, true), this.color || []);

            } else {
                this.mergedColor = this.defaultColor
            }
        },
        initLine () {
            /**
         * Scene
         */
            var scene = new THREE.Scene();
            // var axesHelper = new THREE.AxesHelper(300);
            // scene.add(axesHelper);
            window.bingscene = scene

            /**
             * 
             */
            var geometry = new THREE.BufferGeometry(); //
            // 
            var curve = new THREE.CatmullRomCurve3([
                new THREE.Vector3(100, 0, 0),
                new THREE.Vector3(50, 25, 0),
                new THREE.Vector3(0, 0, 0),

            ]);
            //
            var points = curve.getSpacedPoints(100); //100101
            // setFromPointspointsattributes.position

            geometry.setFromPoints(points);
            var material = new THREE.LineBasicMaterial({
                color: this.mergedColor[0], //
            });
            //
            var line = new THREE.Line(geometry, material);
            scene.add(line);

            var index = 20; //
            var num = 10; //
            var points2 = points.slice(index, index + num); //
            var geometry2 = new THREE.BufferGeometry();
            geometry2.setFromPoints(points2);


            // 
            var posNum = points2.length - 2; //
            var colorArr = [];
            for (var i = 0; i < points2.length; i++) {
                var color1 = new THREE.Color(this.mergedColor[0]); // 
                var color2 = new THREE.Color(this.mergedColor[1]); //
                var color = null;
                // 
                if (i < posNum) {
                    color = color1.lerp(color2, i / posNum)
                } else {
                    color = color2.lerp(color1, (i - posNum) / (points2.length - posNum))
                }
                colorArr.push(color.r, color.g, color.b);
            }
            // 
            geometry2.attributes.color = new THREE.BufferAttribute(new Float32Array(colorArr), 3);


            var material2 = new THREE.LineBasicMaterial({
                // color: 0xffff00, //
                vertexColors: THREE.VertexColors, //color
            });
            //
            var line2 = new THREE.Line(geometry2, material2);
            scene.add(line2);

            /**
             * 
             */
            var width = this.width; //
            var height = this.height; //
            var k = width / height; //
            var s = 40; //
            //
            var camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000);
            // camera.position.set(258.67830365485716, 2.341067217446586, 125); //
            camera.position.set(54, 72, 175);
            camera.lookAt(camera.position); //()
            window.camera = camera
            /**
             * 
             */
            var renderer = new THREE.WebGLRenderer({
                antialias: true, //
            });
            renderer.setSize(width, height); //
            renderer.setClearColor(this.backgroundColor, 1); //
            document.getElementById('threeline1').appendChild(renderer.domElement);

            scene.add(tag(this.textName[0], 0, 0, 0, this.fontSize));
            scene.add(tag(this.textName[1], 100, 0, 0, this.fontSize));
            var labelRenderer = createLabelRender('threeline1', this.width, this.height)


            var cityPointGroup = new THREE.Group();//
            cityPointGroup.position.z = 0;

            var mesh1 = shapeMesh(16, 0, 0, this.mergedColor[1]);
            cityPointGroup.add(mesh1);
            var mesh2 = shapeMesh(16, 100, 0, this.mergedColor[1]);
            cityPointGroup.add(mesh2);

            scene.add(cityPointGroup);


            // 
            var indexMax = points.length - num; //
            var _s = 10.0;
            function render () {

                if (cityPointGroup.children.length) {
                    _s += 0.1;
                    cityPointGroup.children.forEach(function (mesh) {
                        mesh.scale.set(_s, _s, _s);
                        if (_s <= 15) {
                            mesh.material.opacity = 0.5;//1.671/(2.6-2.0)0~1
                        } else if (_s > 15 && _s <= 25) {
                            mesh.material.opacity = 1 - (_s - 10) / 15;//5.00 2.01
                        } else {
                            _s = 10;
                        }
                    })
                }

                if (index > indexMax) index = 0;
                index += 1
                points2 = points.slice(index, index + num); //
                geometry2.setFromPoints(points2);

                labelRenderer.render(scene, camera);
                renderer.render(scene, camera);
                requestAnimationFrame(render);
            }
            render();
            //  camera   
            if (this.control3d) {
                var controls = new OrbitControls(camera, renderer.domElement);
                controls.target.set(54, 15, 31);
                controls.update();//update()camera.lookAt(controls.target)

            }


        }


    }
};
</script>

<style lang="scss" scoped>
.threeLine {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 6px;

    .slot-content {
        position: relative;
        padding: 20px;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }


    .dev-border {
        // position: absolute;
        // display: block;
    }



}
</style>
